import React from 'react';
import AboutHeader from './AboutHeader';
import TechnologyContent from './TechnologyContent';
import { Row } from 'antd';

const spacing = { xs: 8, sm: 16, md: 24, lg: 32 }

function About() {

    return (
        // <div class="site-card-wrapper">
        <div>
            <AboutHeader />
            <Row justify="center" gutter={spacing} style={{marginTop: '50px', width: '100%'}}>
                <TechnologyContent
                    altText="html5"
                    source="https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/250px-HTML5_logo_and_wordmark.svg.png"
                />
                <TechnologyContent
                    altText="css3"
                    source="https://upload.wikimedia.org/wikipedia/commons/thumb/d/d5/CSS3_logo_and_wordmark.svg/1452px-CSS3_logo_and_wordmark.svg.png"
                />
                <TechnologyContent
                    altText="Javascript"
                    source="https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/480px-Unofficial_JavaScript_logo_2.svg.png"
                />
                <TechnologyContent
                    altText="R Programming"
                    source="https://image.pngaaa.com/846/1525846-middle.png"
                />
                <TechnologyContent
                    altText="Python"
                    source="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/2048px-Python-logo-notext.svg.png"
                />
                <TechnologyContent
                    altText="Node.js"
                    source="data:image/png;base64,"
                />
            </Row>
        </div>
    );

}

export default About;