import React from "react";
import { Row } from "antd";
import PortfolioCard from "./PortfolioCard";
import PortfolioHeader from "./PortfolioHeader";

const spacing = { xs: 8, sm: 16, md: 24, lg: 32 };

function Portfolio() {
  return (
    <div className="site-card-wrapper">
      <PortfolioHeader />
      <Row
        justify="center"
        gutter={spacing}
        style={{ marginTop: "50px", width: "100%" }}
      >
        <PortfolioCard
          gitHubLink="https://github.com/DataDeveloper7865/jobly-final"
          imgLink="https://www.ivywise.com/core/wp-content/uploads/2017/01/What-Happens-to-Your-College-Application-After-Its-Submitted-1024x1024.jpg"
          cardDescription="Mock Job Board Application"
          cardTitle="Jobly"
          techStack="Postgres, React, Express.js"
        />
        <PortfolioCard
          gitHubLink="https://github.com/DataDeveloper7865/warbler"
          imgLink="data:image/jpeg;base64,"
          cardDescription="Twitter Clone"
          cardTitle="Warbler"
          techStack="Postgres, React, Express.js"
        />
        <PortfolioCard
          gitHubLink="https://github.com/DataDeveloper7865/wgu-comp-sci-final-front-end"
          imgLink="https://erpinnews.com/wp-content/uploads/2018/12/web-marketing-analytics-1.jpg"
          cardDescription="Finnhub REST API consumption"
          cardTitle="Finnhub API Consumption"
          techStack="React, Google App Engine, Plotly.js"
        />
      </Row>
    </div>
  );
}

export default Portfolio;
